import React from "react";
import s from './Post.module.css';

const Post = (props) => {
  return (
    <div className={`${s.item} ${s.active}`}>
      <img src="data:image/jpeg;base64,"></img>
      <div>
        {props.message}
      </div>
      <span>like {props.likesCount}</span>
    </div>
  )
}

export default Post;